import React, { useState, useEffect } from "react";
import moment from "moment";
import "antd/dist/antd.css";
import "moment/locale/vi";
import {
  Form,
  Select,
  Button,
  Table,
  Popover,
  Divider,
  Typography
} from "antd";
import { Link } from "react-router-dom";
import Axios from "axios";

const SelectTable = ({ id, token }) => {
  const [toDelete, setDelete] = useState(true);
  const [deleteRows, setDelRows] = useState([]);
  const [configTime, setCfgTime] = useState(null);
  const [autoMode, setMode] = useState(false);
  const [keepRows, setKeepRows] = useState([]);
  const [data, setData] = useState([]);
  const [status, setStatus] = useState([]);
  const [form] = Form.useForm();

  useEffect(() => {
    Axios.get(
      `https://graph.facebook.com/v6.0/${id}/comments?access_token=${token}`
    ).then(response => {
      setData(response.data.data);
    });
    console.log("Loading dữ liệu");
  }, []);

  const initialState = id => {
    setData(
      data.filter(item => {
        return item.id !== id;
      })
    );
  };

  const handleDelete = () => {
    var deleteStatus = [];

    for (let i in deleteRows) {
      if (
        keepRows.includes(
          keepRows.find(item => item.id === deleteRows[i].id)
        ) ||
        deleteRows.length === 0
      ) {
        deleteStatus.push(0);
      } else {
        deleteStatus.push(1);
      }
    }
    let testResult = deleteStatus.every(item => {
      return item === 1;
    });
    if (testResult) {
      for (let i = 0; i < deleteRows.length; i++) {
        Axios.delete(
          `https://graph.facebook.com/v6.0/${deleteRows[i].id}?access_token=${token}`
        )
          .then(response => {
            // Handling UI
            initialState(deleteRows[i].id);
            // alert("Xóa thành công");
          })
          .catch(error => {
            console.log("Xóa thất bại");
            console.log(error);
          });
      }
      // alert("Xóa thành công");
      console.log("Xóa thành công");
    } else {
      alert("Không thể xóa những comments đã khóa");
    }
  };

  const handleKeepData = () => {
    setKeepRows(deleteRows);
    setDelRows([]);
    setDelete(true);
  };

  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      if (selectedRows.length > 0) {
        setDelete(false);
        setDelRows(selectedRows);
      } else {
        setDelete(true);
      }
    },
    getCheckboxProps: record => ({
      name: record.name
    })
  };

  const autoStart = () => {
    setDelete(true);
    setMode(true);
    setStatus(prevState => [
      ...prevState,
      `Chuẩn bị chạy tự động, thời gian mỗi lần là ${configTime} ms`
    ]);
    setInterval(() => {
      setStatus(prevState => [...prevState, "Bắt đầu chạy tự động"]);
      var deleteData = [];
      Axios.get(
        `https://graph.facebook.com/v6.0/${id}/comments?access_token=${token}`
      ).then(response => {
        setData(response.data.data);
        // console.log(response.data.data);
        // console.log(keepRows);
        if (response.data.data.length === keepRows.length) {
          // console.log("Không có gì để xóa");
          setStatus(prevState => [
            ...prevState,
            "Không có gì để xóa, chuẩn bị lần chạy tiếp theo"
          ]);
          // console.log(deleteData);
        } else {
          for (let i = 0; i < response.data.data.length; i++) {
            if (
              !keepRows.includes(
                keepRows.find(item => item.id === response.data.data[i].id)
              )
            ) {
              deleteData.push(response.data.data[i]);
            }
          }
          // console.log("Sau khi đưa vào hàng chờ");
          setStatus(prevState => [
            ...prevState,
            `Phát hiện ${deleteData.length} comments cần được xóa`
          ]);

          // console.log(deleteData);
          deleteData.forEach(item => {
            Axios.delete(
              `https://graph.facebook.com/v6.0/${item.id}?access_token=${token}`
            )
              .then(res => {
                // console.log("Xóa thành công");
                setStatus(prevState => [...prevState, "Xóa thành công"]);
                setData(response.data.data);
                deleteData = deleteData.filter(dt => {
                  return dt.id !== item.id;
                });
              })
              .catch(error => {
                setStatus(prevState => [...prevState, "Xóa thất bại"]);
                console.log(error);
              });
          });
          // console.log("Sau khi hoàn tất xóa: ");
          // console.log(deleteData.length);
          // console.log(deleteData);
        }
      });
    }, configTime);
  };

  const cancelAuto = () => {
    alert("Hủy auto thành công");
    window.location.replace("/xoacoment");
  };

  const handleConfigChange = event => {
    // setCfgTime(event.target.value);
    // console.log(event);
    setCfgTime(event);
    // console.log(event);
    // console.log(configTime);
  };

  const content = (
    <Form form={form}>
      <Form.Item label="Thời gian">
        <Select onChange={handleConfigChange} style={{ width: 100 }}>
          <Select.Option value="5000">5 giây</Select.Option>
          <Select.Option selected value="10000">
            10 giây
          </Select.Option>
          <Select.Option value="30000">30 giây</Select.Option>
          <Select.Option value="60000">1 phút</Select.Option>
          <Select.Option value="300000">5 phút</Select.Option>
        </Select>
      </Form.Item>
      <Divider />
      <Button
        type="danger"
        size="middle"
        style={{ marginRight: 10 }}
        onClick={() => autoStart(id)}
      >
        Bắt đầu
      </Button>
      <Button type="ghost" size="middle" onClick={() => cancelAuto()}>
        Hủy
      </Button>
    </Form>
  );
  const text = <h2>Cài đặt tự động</h2>;

  const columns = [
    {
      title: "Mã bình luận",
      dataIndex: "id",
      key: "id"
    },
    {
      title: "Thời gian tạo",
      key: "created_time",
      render: (text, record) => (
        <span>{moment(record.created_time).format("LLLL")}</span>
      )
    },
    {
      title: "Thông tin bình luận",
      key: "message",
      dataIndex: "message"
    }
  ];

  const { Title } = Typography;
  return (
    <div>
      {/* <h1 style={{ padding: 10, margin: 10 }}>Quản lý bài viết {id}</h1> */}
      <Button
        style={{ margin: 12 }}
        type="primary"
        size="large"
        disabled={toDelete}
        // onClick={handleDelete()}
        onClick={() => handleDelete(deleteRows)}
      >
        Xóa
      </Button>

      <Button
        style={{
          margin: 12,
          backgroundColor: toDelete ? "#f7f7f7" : "green",
          color: toDelete ? "#cccccc" : "white"
        }}
        type="primary"
        size="large"
        disabled={toDelete}
        onClick={handleKeepData}
      >
        Giữ
      </Button>
      <Popover
        title={text}
        content={content}
        trigger="click"
        placement="bottom"
      >
        <Button style={{ margin: 10 }} type="danger" size="large">
          Xóa tự động
        </Button>
      </Popover>
      <Link style={{ fontSize: 20, fontWeight: 400, marginLeft: 10 }} to="/">
        Trở về trang chủ
      </Link>
      <Table
        rowSelection={{
          type: "checkbox",
          ...rowSelection
        }}
        dataSource={data}
        columns={columns}
        rowKey="id"
        title={() => `Quản lý bài viết ${id}`}
        footer={() => `Danh sách bao gồm ${data.length} comments`}
      />
      {autoMode ? (
        <div className="status-board" style={{ margin: 10, width: 600 }}>
          <Title type="warning" style={{ fontSize: 18 }}>
            Trạng thái chạy Auto
          </Title>
          <div
            className="status"
            style={{
              fontSize: 15,
              backgroundColor: "gainsboro",
              display: "flex",
              flexDirection: "column"
            }}
          >
            {status.map(item => {
              return <code style={{ padding: 10 }}>{item}</code>;
            })}
          </div>
        </div>
      ) : (
        <p></p>
      )}

      <div class="auto-section">
        <div className="keep-id">
          {keepRows.length > 0 ? (
            <div style={{ margin: 20 }}>
              <Table
                dataSource={keepRows}
                columns={columns}
                rowKey="id"
                title={() => `Danh sách các comment giữ lại`}
                footer={() => `Bao gồm ${keepRows.length} comments`}
              />
            </div>
          ) : (
            <p></p>
          )}
        </div>
      </div>
    </div>
  );
};

export default SelectTable;
