import React, { useState } from "react";
import "./App.css";
import FacebookLogin from "react-facebook-login";
import axios from "axios";
import moment from "moment";
import "antd/dist/antd.css";
import "moment/locale/vi";

import { Select, Form, Input, Button, Radio } from "antd";
import { Link } from "react-router-dom";

moment.locale("vi");
function App() {
  const [facebookName, setFBName] = useState([]);
  const [pageToken, setToken] = useState(null);
  const [posts, setPosts] = useState([]);
  const [page, setPage] = useState(null);
  const [post, setPost] = useState(null);
  const [form] = Form.useForm();

  const responseFacebook = response => {
    fetchUserFeed(response.accessToken);
    console.log(response);
  };

  const fetchUserFeed = access => {
    axios
      .get(`https://graph.facebook.com/v6.0/me?access_token=${access}`)
      .then(response => {
        // setFBName(response.data.data[0].name);
        setToken(access);
        setFBName(response.data);
        console.log(response.data);
        // console.log("Good");
      });
  };

  const handleChangePost = event => {
    console.log(event.target.value);
    setPost(event.target.value);
  };
  const handleChangePage = event => {
    setPage(event.target.value);
  };
  const handleSubmit = event => {
    event.preventDefault();
    // console.log(event);
    axios
      .get(
        `https://graph.facebook.com/v6.0/${page}_${post}?access_token=${pageToken}`
      )
      .then(response => {
        console.log(response.data);
        setPosts(response.data);
        console.log(posts);
      })
      .catch(error => {
        console.log(error);
      });
    axios
      .get(
        `https://graph.facebook.com/v6.0/${page}?fields=access_token&access_token=${pageToken}`
      )
      .then(res => {
        console.log(res.data.access_token);
        setToken(res.data.access_token);
      })
      .catch(err => {
        console.log(err);
      });
  };

  return (
    <div className="App">
      <h1>
        <Link to="/">Comment Delete Tool</Link>
      </h1>
      <p>Powered by Maximusss - Leauge Of Legends Support Semi-pro gamer.</p>

      {facebookName.name ? (
        <div>
          <Form
            form={form}
            style={{ display: "flex", justifyContent: "center" }}
          >
            <Form.Item label="Mã trang" style={{ marginRight: 20 }}>
              <Input
                name="page"
                onChange={handleChangePage}
                placeholder="Nhập mã trang"
              />
            </Form.Item>
            <Form.Item label="Mã bài viết">
              <Input
                name="post"
                onChange={handleChangePost}
                placeholder="Nhập mã bài viết"
              />
            </Form.Item>
            <Form.Item>
              <Button type="primary" onClick={handleSubmit}>
                Submit
              </Button>
            </Form.Item>
          </Form>
          {posts.message ? (
            <div>
              <h1>Bài viết {post}</h1>
              <h3>{posts.message}</h3>
              <h3>{posts.created_time}</h3>
              <h3>{pageToken}</h3>
              <Link
                // onClick={() => console.log("Token của tôi là " + token)}
                to={{
                  pathname: `/post/${post}`,
                  state: { id: `${page}_${post}`, access_token: pageToken }
                }}
              >
                Chạy
              </Link>
            </div>
          ) : (
            ""
          )}

          {/* <Tabular data={} token={pageToken} /> */}
        </div>
      ) : (
        <FacebookLogin
          appId="2623573851104227"
          autoLoad={true}
          size="small"
          fields="pages_show_list,manage_pages,publish_pages,ads_management,public_profile,business_management,ads_read"
          callback={responseFacebook}
        />
      )}
    </div>
  );
}

export default App;
